.container-form {
  background-color: hsl(47, 62%, 75%);
  border: solid 0.5rem hsl(33, 53%, 34%);
  border-radius: 1rem;
  color: black;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  font-weight: 600;
  padding: 1rem;
  text-align: center;
  width: 420px;
}

.container-form label {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.5rem;
}

.container-form-attr {
  display: flex;
  justify-content: space-evenly;
}


.container-form-attr label {
  gap: 0.5rem;
  width: 20%;
}

input, select {
  border: 0.15rem solid hsl(33, 53%, 34%);
  border-radius: 4px;
  outline: none;
  padding: 6px;
  text-align: center;
}

select {
  width: 40%;
}

.container-form-rare label {
  align-items: center;
  display: flex;
  justify-content: center;
}

.container-form-trunfo label {
  display: flex;
  flex-direction: row;
  justify-content: center;
}


.container-form button {
  background-color: #782926;
  border: none;
  border-radius: 0.5rem;
  color: black;
  font-size: 1rem;
  font-weight: 600;
  height: 2rem;
}

.container-form button:disabled {
  background-color: #782926d7;
}
