.container-card {
  background-color: hsl(47, 62%, 75%);
  border: solid 0.5rem hsl(33, 53%, 34%);
  border-radius: 1rem;
  color: black;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  font-weight: 600;
  height: 562px;
  padding: 1rem;
  text-align: center;
  width: 420px;
}

.container-card-top {
  align-items: center;
  display: flex;
  justify-content: space-around;
}

.container-card img {
  border: 0.2rem solid hsl(33, 53%, 34%);
  border-radius: 4px;
  height: 250px;
  width: 420px;
}

.container-card-attr {
  display: flex;
  justify-content: space-around;
}

.container-card-attr p {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.container-card-description {
  background-color: hsl(33, 53%, 34%);
  border-radius: 4px;
  height: 150px;
  padding: 1rem;
  width: auto;
}
